<template>
    <div class="prod-list-infor">
        <el-table
            :data="infor"
            border
            @row-dbclick="goForDetail">
            <el-table-column
                type="index"
                label=""
                width="50%">
            </el-table-column>
            <el-table-column
                prop="ogId"
                label="ID">
            </el-table-column>
            <el-table-column
                prop="ogName"
                label="">
            </el-table-column>
            <el-table-column
                prop="ogAttribute"
                label="">
            </el-table-column>
            <el-table-column
                prop="ogType"
                label="">
            </el-table-column>
            <el-table-column
                prop="ogStatus"
                label="">
            </el-table-column>
            <el-table-column
                prop="gmtCreate"
                label="">
            </el-table-column>
            <el-table-column
                prop="gmtModified"
                label="">
            </el-table-column>
        </el-table>
    </div>
</template>
<script>
export default {
    props: {
        infor: {
            type: Array,
            required: true
        }
    },
    data() {
        return {
        }
    },
    methods: {
        goForDetail() {
            this.$router.push()
        }
    }
}
</script>
<style lang="stylus">

</style>



